/* Külső háttér, teljes képernyőre */
.outer-background {
  width: 100vw;
  min-height: 100vh;
  background-color: #b82eb4;
  display: flex;
  justify-content: center;
  padding: 40px 20px;
  box-sizing: border-box;
}

/* Belső konténer, középre zárva */
.post-detail-container {
  width: 100%;
  max-width: 960px; /* nem teljes szélességű, de elég tág */
  background: #8ff5f4;
  border-radius: 12px;
  padding: 40px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}


/* Cím stílus */
.post-title {
  color: #38064a;
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-align: center;
}

/* Tartalom */
.post-content {
  white-space: pre-wrap;
  line-height: 1.6;
  color: #38064a;
  max-width: 900px;
  margin: 0 auto;
}

/* Időbélyeg */
.timestamp {
  font-size: 12px;
  color: #999;
  text-align: center;
  margin-top: 20px;
}

/* Navigáció */
.nav-container {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  margin-top: 40px;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

.nav-button {
  padding: 10px 20px;
  background-color: #eee;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  color: #6a4c9c;
  font-weight: bold;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.nav-button:hover {
  background-color: #ff6c17;
  color: white;
}

.nav-title {
  font-size: 14px;
  color: #6a4c9c;
  text-align: center;
  margin-top: 8px;
}
