.about-container {
  background-color: #8ff5f4;
  padding: 15px 40px 40px 40px; /* Csak a felső padding csökkentése */
}

.about-title {
  font-size: 32px;
  text-align: left;
  margin-left: 10px; /* A sárga doboz bal oldala fölé igazítva */
  color: #000033;
  margin-bottom: 3%;
}

.about-box {
  background-color: #f1bb00;
  padding: 30px;
  margin: 20 auto;
  max-width: 1650px;
}

.about-content {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 30px;
}

.about-image {
  width: 35vw;
  height: auto;
  object-fit: cover;
  flex-shrink: 0;
}

.about-text {
  color: #2c76c1;
  font-size: 20px;
  text-align: left;
  line-height: 1.6;
  max-width: 500px;
}

.about-footer {
  background-color: white;
  color: black;
  font-size: 14px;
  padding: 20px;
  margin-top: 40px;
  text-align: center;
}


