.posts-section {
  width: 100%;
  padding: 40px 60px;
  background: #8ff5f4;
  border-radius: 0; /* már nincs szükség a körívre, mert teljes szélesség */
  box-sizing: border-box;
}

.posts-section h2 {
  text-align: center;
  color: #6a4c9c;
  font-size: 2.5rem;
  margin-bottom: 40px;
}

.posts-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 🔒 fixen 3 oszlop */
  gap: 24px;
}

.post-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 16px;
  padding: 24px;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 220px;
}

.post-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.post-card h3 {
  margin-bottom: 12px;
  font-size: 1.3rem;
  color: #6a4c9c;
}

.post-card p {
  font-size: 1rem;
  color: #555;
}

.timestamp {
  font-size: 0.85rem;
  color: #999;
  margin-top: 16px;
  text-align: left;
}

/* 🔻 Reszponzivitás: kisebb képernyőkön is 3 oszlop, de kisebb méret */
@media (max-width: 1000px) {
  .posts-section {
    padding: 30px 10px;
  }

  .post-card {
    padding: 16px;
    font-size: 0.95rem;
  }

  .post-card h3 {
    font-size: 1.1rem;
  }
}

@media (max-width: 700px) {
  .posts-grid {
    grid-template-columns: repeat(3, 1fr); /* Marad 3 oszlop! */
    gap: 12px;
  }

  .post-card {
    padding: 12px;
    font-size: 0.9rem;
  }

  .post-card h3 {
    font-size: 1rem;
  }
}
