/* src/pages/Home.css */

.blue-section {
    background-color: #9cf1f1;
    padding: 40px;
    text-align: left;
  }
  
  .blue-section h2 {
    margin: 0;
    font-size: 2rem;
  }
  
  .long-text-section {
    background-color: #e7cfcf;
    padding: 40px;
    text-align: center;
    width: 100%;
    box-sizing: border-box;
  }
  
  .long-text-section h3 {
    font-size: 64px;
    color: #4a148c;
    margin-bottom: 20px;
    max-width: 800px;
    margin: 0 auto 20px auto;
  }
  
  .long-text-section p {
    font-size: 18px;
    color: #4a148c;
    line-height: 1.6;
    margin-bottom: 10px;
    max-width: 650px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  
  .dark-purple-section {
    background-color: #af5f98;
    padding: 40px;
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  
  .image-container {
    display: flex;
    justify-content: center;
    gap: 10vw;
  }
  
  .img-one {
    width: 18vw;
    height: calc(18vw * 1.3);
    object-fit: cover;
    border-radius: 8px;
  }
  
  .img-two {
    width: 26.6vw;
    height: calc(26.6vw * 1.0125);
    object-fit: cover;
    border-radius: 8px;
  }
  
  .img-three {
    width: 23.3vw;
    height: calc(23.3vw);
    object-fit: cover;
    border-radius: 8px;
  }
  
  .forest-image {
    background-image: url('forest.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
    height: 30vh;
    width: 100%;
  }
  
  .light-blue-section {
    background-color: #9cf1f1;
    height: 100px;
  }
  
  .scroll-button-container {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: -30px;
    position: relative;
    z-index: 1;
  }
  
  .scroll-button {
    background-color: red;
    color: white;
    font-size: 20px;
    font-weight: bold;
    padding: 14px 40px;
    border: none;
    border-radius: 50px;
    transition: background-color 0.3s ease;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  }
  
  .scroll-button:hover {
    background-color: #0066cc;
  }
  