    .privacy-container {
        max-width: 900px;
        margin: 50px auto;
        padding: 40px;
        background-color: #ffffff;
        border-radius: 16px;
        box-shadow: 0 12px 30px rgba(0, 0, 0, 0.1);
        font-family: 'Roboto', sans-serif;
        color: #444;
        transition: box-shadow 0.3s ease;
    }

    .privacy-container:hover {
        box-shadow: 0 12px 50px rgba(0, 0, 0, 0.2);
    }
  
  .privacy-container h1 {
    font-size: 32px;
    margin-bottom: 20px;
    color: #ff6c17;
    text-align: center;
  }
  
  .privacy-container p {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .privacy-container code {
    background-color: #f4f4f4;
    padding: 2px 6px;
    border-radius: 4px;
    font-family: monospace;
    font-size: 14px;
  }
  
  /* Narancssárga szín a JSON kifejezéshez */
  .json-text {
    color: #ff6c17;
    font-weight: bold;
  }
  
  /* CSS a narancssárga e-mail linkhez */
  .email-link {
    color: #ff6c17;
    font-weight: bold;
    cursor: pointer;
  }
  
  .email-link:hover {
    color: #e55a00; /* Sötétebb narancssárga hover szín */
  }

  
  