.subscribe-container {
  background: #7cecf1;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 20px;
  font-family: Concert One;
}

.subscribe-box {
  background: linear-gradient(to bottom, #3ad2dc, #c8eae6);
  border-radius: 25px;
  padding: 40px 30px;
  max-width: 500px;
  width: 33%;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.subscribe-box h1 {
  color: white;
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 15px;
}

.subscribe-box p {
  color: white;
  font-size: 20px;
  margin-bottom: 25px;
}

.subscribe-box input {
  width: 90%;
  padding: 15px;
  font-size: 20px;
  margin-bottom: 20px;
  border-radius: 6px;
  border: none;
  outline: none;
  font-family: Concert One;
}

.subscribe-box button {
  background-color: #ff6c17;
  color: white;
  font-weight: bold;
  padding: 15px;
  font-size: 30px;
  border: none;
  border-radius: 35px;
  cursor: pointer;
  width: 100%;
  margin-bottom: 20px;
  font-family: Concert One;
}

.subscribe-box .privacy-note {
  font-size: 14px;
  color: white;
}

.privacy-note a {
  color: #2e3ebf;
  text-decoration: underline;
}
