.blue-section {
    background-color: #9cf1f1;
    padding: 40px;
    text-align: left;
  }
  
  .blue-section h2 {
    margin: 0;
    font-size: 2rem;
  }
  
  .blue-section ul {
    list-style-type: none;
    padding: 0;
    margin-top: 20px;
  }
  
  .blue-section li {
    font-size: 18px;
    color: #2870ff;
    line-height: 2.4; /* Megnövelt sorköz */
    margin-bottom: 14px; /* Plusz térköz az elemek között */
  }
  
  .blue-section a {
    text-decoration: none;
    color: #2870ff;
    transition: color 0.3s ease;
  }
  
  .blue-section a:hover {
    color: #ff6c17;
  }
  